const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: [],
			imgUrl: '',
			nextPageUrl: '',
			token: ''
		};
	},
	onLoad() {
		this.token = uni.getStorageSync('token')
		this.imgUrl = helper.imgUrl;
		// 加载查稿列表
		this.myDraftPosts()
	},
	onReachBottom() {
		// 页面上拉触底事件的处理函数
		if (this.nextPageUrl != '') {
			this.getMore()
		}
	},
	methods: {
		deleteItem(e) {
			// 点击删除
			uni.showModal({
				title: '提示',
				content: '确认删除?',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						let index = e.currentTarget.dataset.index
						this.runDeleteItem(index)
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		runDeleteItem(index) {
			// 执行草稿删除
			uni.showLoading({
			  title: '删除中...'
			});
			let list = this.list
			uni.request({
				url: `${helper.requestUrl}/posts/del`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					post_id: list[index].id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'success',
							duration: 2000
						});
						let newList = []
						for (let i = 0; i < list.length; i++) {
							if (list[i].id != list[index].id) {
								newList.push(list[i])
							}
						}
						this.list = newList
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		goDetails(e) {
			let id = e.currentTarget.dataset.id
			uni.navigateTo({
				url: `/pages/articleDetail?id=${id}`
			})
		},
		myDraftPosts() {
			// 加载查稿列表
			uni.showLoading({
				title: '列表获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/user/my-draft-posts`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res, '++++++++')
					if (res.data.status_code == 200) {
						this.list = res.data.data
						this.nextPageUrl = res.data.next_page_url
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			})
		},
		getMore() {
			// 加载更多
			uni.showLoading({
				title: '列表获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/user/my-draft-posts`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res, '++++++++')
					if (res.data.status_code == 200) {
						let list = this.list
						let newList = res.data.data
						this.list = list.concat(newList)
						this.nextPageUrl = res.data.next_page_url
					} else {
						uni.showToast({
							title: res.data.message
						});
						this.nextPageUrl = ''
					}
				}
			})
		}
	}
}
